.divKioku {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    height: auto;
    max-width: 450px;
}

.kiokuAnimation {
    -webkit-animation: fadein 7s;
    animation: fadein 7s;
}

.rowKiokuStyle {
    height: 100vh;
    background: -webkit-linear-gradient(#ebeec2, #ffdbd2);
    background: -o-linear-gradient(#ebeec2, #ffdbd2);
    background: -webkit-gradient(linear, left top, left bottom, from(#ebeec2), to(#ffdbd2));
    background: linear-gradient(#ebeec2, #ffdbd2);

}

@keyframes fadein {
    from {opacity: 0;}
    to {opacity: 1;}
}

@-webkit-keyframes fadein {
    from {opacity: 0;}
    to {opacity: 1;}
}


